@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Nabla&display=swap);
.ads {
  margin: 30px;
  min-height: 70vh;
  display: flex;
  flex-wrap: wrap;
}

.ads * {
  font-family: "Josefin sans";
}

.rim {
  background-color: red;
  height: 420px;
  width: 300px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: blink 5s infinite;
          animation: blink 5s infinite;
}

@keyframes rim {
  0% {
    background: red;
  }
  20% {
    background: green;
  }
  40% {
    background: yellow;
  }
  60% {
    background: blue;
  }
  80% {
    background: orange;
  }
  100% {
    background: red;
  }
}
@-webkit-keyframes rim {
  0% {
    background: red;
  }
  20% {
    background: green;
  }
  40% {
    background: yellow;
  }
  60% {
    background: blue;
  }
  80% {
    background: orange;
  }
  100% {
    background: red;
  }
}

.ad {
  height: 415px;
  width: 295px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ad-img {
  width: 100%;
  height: 50%;
}

.at {
  font-size: 20px;
  font-weight: 700;
}

.ad-description {
  text-align: center;
  margin: 5px 5px;
  border: 1px solid gray;
  border-radius: 5px;
}

.abt {
  display: flex;
  justify-content: space-around;
  border-radius: 5px;
  width: 100%;
}

.spinx {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.blink {
  width: 42%;
  height: 170px;
  -webkit-animation: blink 5s infinite;
          animation: blink 5s infinite;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}
@keyframes blink {
  0% {
    background: red;
  }
  20% {
    background: green;
  }
  40% {
    background: yellow;
  }
  60% {
    background: blue;
  }
  80% {
    background: orange;
  }
  100% {
    background: red;
  }
}
@-webkit-keyframes blink {
  0% {
    background: red;
  }
  20% {
    background: green;
  }
  40% {
    background: yellow;
  }
  60% {
    background: blue;
  }
  80% {
    background: orange;
  }
  100% {
    background: red;
  }
}
.nav {
  background-color: blue;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-area {
  width: 99%;
  height: 155px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: blue;
}

.nav-title {
  font-size: 3rem;
  font-style: italic;
  font-family: "Nabla", cursive;
}

.title-sub {
  font-size: 1rem;
  font-style: normal;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-bottom: 5px;
  margin-top: 10px;
}

.link {
  font-family: "Josefin Sans", sans-serif;
  background-color: white;
  padding: 2px 5px;
  border-radius: 5px;
}

a {
  text-decoration: none;
}

.balance {
  font-weight: 700;  
}

.link:hover {
  cursor: pointer;
  box-shadow: 3px 3px 2px black;
}

.dsb table {    
    width: 98vw;
    border: solid 1px blue;
}

.dsb table tr, .dsb table td {
    margin: 10px;
    border-top: solid 1px blue;
    font-size: 20px;
    font-family: "Josefin sans";
    padding: 20px;
}

.dsb {
    height: 100vh;
}
.footer {
    background-color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    height: 50px;
    font-family: "Josefin sans";
}
body {
  margin: 0;
  padding: 0;
}

